// @import "node_modules/bootstrap-sass/assets/stylesheets/bootstrap";

.navbar {
    height: 80px;
    background-color: white;
    opacity: .8;
}

.navbar-brand {
    padding-top: 10px;
    font-family: "proxima-nova";
}

.navbar-collapse.collapsing, .navbar-collapse.collapse.in {
    float: right;
    background-color: #FFF0E0;
    text-align: center;
    opacity: .93;
    border-radius: 10px;
    border-style: solid;
}

#navbar-button {
    margin-top: 25px;
}

#navbar {
    padding-top: 17px;
}

#content {
    padding-top: 100px;
}

body {
    background-color: #EBE0D6;
    padding-bottom: 100px;
    background-image: url("../img/center_ridge.jpeg");
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
}

.jumbotron {
    background-color: white;
    opacity: .93;
}

#prof-pic {
    opacity: 1;
    border-radius: 20px;
}

a {
    color: #669999;
}

a:hover {
    color: #DEC3C3;
    font-style: oblique;
    text-decoration: none;
}

#about, #contact, #projects, #reading {
    color: black;
}

#name, .jumbotron {
    text-align: center;
    font-family: "proxima-nova";
    letter-spacing: 20px;
    font-style: italic;
    font-weight: 300;
    font-size: 54px;
    line-height: 1.6em;
}

/** About Page */
#about-content, #contact-content, #projects-index-content {
    background-color: white;
    opacity: .93;
    border-radius: 20px;
    padding: 30px;
    font-family: "proxima-nova";
}

#tech-skills-row {
    padding-left: 5%;
    padding-right: 5%;
    text-align: center;
}

.logos {
    padding-bottom: 5%;
}

.resume-link {
    text-align: center;
    font-family: "proxima-nova";
    letter-spacing: 10px;
    font-weight: 300;
    font-size: 20px;
}

/** Contact Page */
.social {
    margin-left: 35%;
}

.email {
    text-align: center;
    font-family: "proxima-nova";
}

/** Projects Page */
.project-links {
    margin-left: 25px;
}

/* ------------ Mobile sizing ------------ */
@media screen and (max-width: 400px) {
    .jumbotron {
        margin-left: 15px;
        margin-right: 15px;
    }

    #contact-content {
        display: inline-block;
        margin-left: 23%;
    }

    .social {
        margin-left: 0%;
    }

    #instagram {
        margin-left: 6%;
        margin-top: 10%;
    }
}
